.container {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 20px 40px;
  border-radius: 5px;

  h1 {
    text-align: center;
    margin-bottom: 30px;
  }

  a {
    text-decoration: none;
    color: lightblue;
  }

  .btn {
    cursor: pointer;
    display: inline-block;
    width: 100%;
    padding: 15px;
    background: lightblue;
    font-family: inherit;
    font-size: 16px;
    border: 0;
    border-radius: 5px;

    &:focus {
      outline: 0;
    }

    &:active {
      transform: scale(0.98);
    }
  }

  .text {
    margin-top: 30px;
  }

  .form-control {
    position: relative;
    margin: 20px 0 40px;
    width: 300px;

    input {
      background-color: transparent;
      border: 0;
      border-bottom: 2px solid #fff;
      display: block;
      width: 100%;
      padding: 15px 0;
      font-size: 18px;
      color: #fff;
    }
    input:focus,
    input:valid {
      outline: 0;
      border-bottom-color: lightblue;
    }

    label {
      position: absolute;
      top: 15px;
      left: 0;
      pointer-events: none;

      span {
        display: inline-block;
        font-size: 18px;
        min-width: 5px;
        transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      }
    }

    input:focus + label span,
    input:valid + label span {
      color: lightblue;
      transform: translateY(-30px);
    }
  }
}
